<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">组件管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
        <el-button
          type="text"
          style="float: right; padding: 3px 0"
          @click="moreSelect"
        ><i :class="selectClass"></i>更多选项</el-button>
        <div
          class="advanced-box"
          v-if="content_show"
        >
          <el-form
            :inline="true"
            :model="formSearch"
            class="demo-form-inline"
          >
            <el-form-item label="组件类型">
              <el-select
                v-model="formSearch.type_code"
                placeholder="请选择"
                style="width:100%"
                @change="handleChange"
              >
                <el-option
                  v-for="item in component_type_list"
                  :key="item.id"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="关联ID" prop="type_id">
              <el-select
                v-model="formSearch.type_id"
                placeholder="请选择"
                style="width:100%"
              >
                <el-option
                  v-for="item in second_select_list"
                  :key="item.type_id"
                  :label="item.title"
                  :value="item.type_id"
                >
                </el-option>
                <div class="text-center" style="position: sticky;background: #fff;height:30px;top:0;z-index:1">
                  <a class="text-normal">
                    <el-pagination 
                      class="pagination"
                      @current-change="handleSelectCurrentChange"
                      :page-sizes="[10]"
                      :page-size="page_select_size"
                      :current-page.sync="currentSelectPage"
                      :total="select_total"
                      layout="prev,pager, next"/>
                  </a>
                </div>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="onSubmit"
              >查询</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </el-row>
    <treeTable 
      :columns="columns" 
      :tree-structure="true" 
      :data="dataSource"
      @on-delete="handleDelete"
      @on-edit="handleEdit"
      @on-add="handleAdd"
    >
    </treeTable>
    <el-dialog
      :title="2 == dialog_type ? '编辑': '新增'"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item label="组件类型" prop="type_code">
            <el-select :disabled="0 != dialog_type"
              v-model="formLabelAlign.type_code"
              placeholder="请选择"
              style="width:100%"
              @change="handleChange"
            >
              <el-option
                v-for="item in component_type_list"
                :key="item.id"
                :label="item.name"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="关联ID" prop="type_id">
            <el-select :disabled="2 == dialog_type"
              v-model="formLabelAlign.type_id"
              placeholder="请选择"
              style="width:100%"
            >
              <el-option
                v-for="item in second_select_list"
                :key="item.type_id"
                :label="item.title"
                :value="item.type_id"
              >
              </el-option>
              <div class="text-center" style="position: sticky;background: #fff;height:30px;top:0;z-index:1">
                <a class="text-normal">
                  <el-pagination 
                    class="pagination"
                    @current-change="handleSelectCurrentChange"
                    :page-sizes="[10]"
                    :page-size="page_select_size"
                    :current-page.sync="currentSelectPage"
                    :total="select_total"
                    layout="prev,pager, next"/>
                </a>
              </div>
            </el-select>
          </el-form-item>
          <!--<el-form-item label="上级节点ID" prop="parent_id">
            <el-input disabled v-model="formLabelAlign.parent_id"></el-input>
          </el-form-item>-->
          <el-form-item label="标题" prop="title">
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item label="副标题">
            <el-input v-model="formLabelAlign.subtitle"></el-input>
          </el-form-item>
          <el-form-item label="编码">
            <el-input v-model="formLabelAlign.code"></el-input>
          </el-form-item>
          <el-form-item
            label="图片"
            prop="icon_name"
          >
            <el-col :span="20">
              <el-input
                v-model="formLabelAlign.icon_name"
                :disabled="true"
              ></el-input>
            </el-col>
            <el-upload
              class="upload-demo"
              action="core/file/upload"
              multiple
              :limit="1"
              :before-upload="beforeUpload"
              :show-file-list='false'
            >
              <el-button
                size="medium"
                type="primary"
              >点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item
            label="content"
            style="margin-bottom:0;"
          >
          </el-form-item>
          <div class="editor-container">
            <ueditor :defaultMsg="formLabelAlign.content" @postEditorToParent="getEditorContent" :config="config" ref="ueditor"></ueditor>
          </div>
          <el-form-item label="摘要">
            <el-input v-model="formLabelAlign.summary"></el-input>
          </el-form-item>
          <el-form-item label="发布状态">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">未发布</el-radio>
              <el-radio :label="1">发布</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="链接">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import {TreeGrid} from '@/components/treeTable'
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
import { prefix } from '@/api';
export default {
  data() {
    return {
      selectClass: "el-icon-arrow-down",
      first_search:true,
      content_show: false,
      formSearch: {
        type_code: "",
        key_word: ""
      },
      dialogVisible:false,
      select_method: "",
      //下拉选项分页
      second_select_list:[],
      page_select_size:10,
      currentSelectPage:1,
      select_total:15,

      //表格分页
      dataSource:[],
      currentPage: 1,
      page_size: 15,
      total: 0,
      columns: [
        {
          text: "组件类型",
          value: "type_code",
          width: 200
        },
        /*{
          text: "上级节点ID",
          value: "parent_id",
          width: 100
        },*/
        {
          text: "标题",
          value: "title",
        },
        {
          text: "副标题",
          value: "subtitle"
        },
        {
          text: "图片",
          value: "icon_info",
          width: 110,
          type: 'img'
        },
        {
          text: "状态",
          value: "status",
          width: 100
        },
      ],
      formLabelAlign: {
        type_code: "",
        type_id: 0,
        parent_id: 0,
        title: "",
        code: "",
        subtitle: "",
        content: "",
        icon: "",
        icon_name: "",
        status: 1,
        link: "",
        sort_no: 0,
        news_ids: []
      },
      rules: {
        type_code: [{ required: true, message: "请选择组件类型", trigger: "blur" }],
        type_id: [{ required: true, message: "请选择关联ID", trigger: "blur" }],
        title: [{ required: true, message: "请输入标题", trigger: "blur" }],
        parent_id: [{ required: true, message: "请输入根据点ID", }],
      },
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350,
        serverUrl:prefix + "core/file/upload?token=" + storageHelper.getItem(authToken, 'healthcare'),
        imageActionName : prefix + "core/file/upload",
        zIndex:10000
      },
      component_type_list: [],
      dialog_type: 0,        //0-添加根节点,1-添加子节点,2-修改节点
      loading: null,
    };
  },
  mounted() {
    //this.getInfoList("1", "15");
    this.getdDictionaryInfo();
  },
  methods: {
    //更多
    moreSelect() {
      if (this.selectClass == "el-icon-arrow-down") {
        this.selectClass = "el-icon-arrow-up";
        this.content_show = true;
      } else {
        this.selectClass = "el-icon-arrow-down";
        this.content_show = false;
      }
    },
    //查询
    onSubmit() {
      this.getInfoList("1", "15");
    },
    //打开添加
    openAddDialog() {
      this.dialog_type = 0;
      this.formLabelAlign = {
        type_code: this.formLabelAlign.type_code,
        type_id: this.formLabelAlign.type_id,
        parent_id: 0,
        title: "",
        code: "",
        subtitle: "",
        content: "",
        icon: "",
        icon_name: "",
        status: 1,
        link: "",
        sort_no: 0,
        news_ids: []
      };
      this.dialogVisible = true;
    },
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    //首个下拉选项
    handleChange(val){
      this.select_method = val;
      this.currentSelectPage = 1;
      this.getSecondSelectList();
    },
    //下拉选项选取页
    handleSelectCurrentChange(val) {
      this.currentSelectPage = val;
      this.getSecondSelectList();
    },
    //表格删除按钮
    handleDelete(index,obj) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        var params = {id: obj.id };
        that.submitInfo("content/component/delete", params, "删除成功!");
      }).catch(() => {});
    },
    //表格编辑按钮
    handleEdit(index,obj) {
      this.dialog_type = 2;
      this.showLoading();
      this.getDetail(obj.id);
      //this.dialogVisible = true;
    },
    getDetail(id){
      var params = {};
      var that = this;
      axios.get("content/component/details/" + id, { params }).then(
        function(response) {
          var code = response.code;
          //console.log(response);
          if (0 == code) {
            var json = response.data;
            var icon_src = "";
            var icon_name = "";
            if (null != json.icon_info) {
              icon_src = json.icon_info.src;
              icon_name = json.icon_info.name;
            }
            json.icon_src = icon_src;
            json.icon_name = icon_name;
            that.formLabelAlign = json;
            if(that.loading){
              that.closeLoading();
            }
            that.dialogVisible = true;
          } else {
          }
        },
        function(error) {
        }
      );
    },
    showLoading(){
      this.loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
    },
    closeLoading(){
      if(this.loading){
        this.loading.close();
      }
    },
    //表格添加按钮
    handleAdd(index,obj){
      this.dialog_type = 1;
      this.formLabelAlign = {
        type_code: obj.type_code,
        type_id: obj.type_id,
        parent_id: obj.id,
        title: "",
        code: "",
        subtitle: "",
        content: "",
        icon: "",
        icon_name: "",
        status: 1,
        link: "",
        sort_no: 0,
        news_ids: []
      };
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      var that = this;
      var fd = new window.FormData();
      fd.append("file", file);
      axios
        .post("core/file/upload", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: storageHelper.getItem(authToken, "healthcare")
          }
        })
        .then(function(res) {
          if (res.code == 0) {
            var url = res.data.url;
            that.formLabelAlign.icon_name = file.name;
            that.formLabelAlign.icon = res.data.code;
            that.$message({
              type: "success",
              message: "上传成功！"
            });
          }
        });
      return false;
    },
    //获取编辑框内容
    getEditorContent(content) {
      this.formLabelAlign.content = content;
    },
    //获取二级下拉选取内容
    getSecondSelectList(){
      var params = {
        page_index: this.currentSelectPage,
        page_size: this.page_select_size
      };
      var that = this;
      var url = "content/" + this.select_method + "/pageList";
      axios.get(url, { params }).then(
        function(res) {
          var code = res.code;
          if (0 == code) {
            var array = [];
            var rdata = res.data;
            that.page_select_size = rdata.page_size;
            that.currentSelectPage = rdata.page_index;
            that.select_total = rdata.count;
            var json = rdata.rows;
            var key = that.select_method + "_id";
            for (var i = 0; i < json.length; i++) {
              var obj = {
                title: json[i].title,
                type_id: json[i][key]
              };
              array.push(obj);
            }
            if(array){
              that.formSearch.type_id = array[0].type_id;

              that.formLabelAlign.type_id = array[0].type_id;
              that.formLabelAlign.parent_id = array[0].type_id;
              that.formLabelAlign.title = array[0].title
            }
            that.second_select_list = array;
            if(that.first_search){
              that.first_search = false;
              that.getInfoList("1", "15");
            }
          }
        },
        function(error) {
        }
      );
    },
    //编辑框提交按钮
    addInfo(formType) {
      var that = this;
      this.showLoading();
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.dialog_type == 2) {
            method = "content/component/update";
            info = "修改成功!";
          } else {
            method = "content/component/create";
            info = "添加成功!";
          }
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          that.closeLoading();
          return false;
        }
      });
    },
    submitInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          that.closeLoading();
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {
          that.closeLoading();
        }
      );
    },
    getInfoList(page_index, page_size){
      var params = {
        type_code: this.formSearch.type_code,
        type_id: this.formSearch.type_id,
        page_index: page_index,
        page_size: page_size
      };
      var that = this;
      axios.get("content/component/treeList", { params }).then(
        function(res) {
          var code = res.code;
          if (0 == code) {
            var array = [];
            var rdata = res.data;
            var des_num = (page_index - 1) * page_size;
            var json = rdata;
            for (var i = 0; i < json.length; i++) {
              var status = json[i].status;
              var status_name = "不显示";
              if (status == 1) {
                status_name = "显示";
              }
              json[i].index = des_num + i + 1;
              json[i].status_name = status_name;
              array.push(json[i]);
            }
            that.dataSource = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    getdDictionaryInfo() {
      var that = this;
      var params = { code: "component_type" };
      axios.get("core/dictionary/list", { params }).then(
        function(res) {
          var code = res.code;
          if (0 == code) {
            that.component_type_list = res.data;
            if(that.component_type_list){
              that.formSearch.type_code = that.component_type_list[0].value;
              that.select_method = that.formSearch.type_code;
              that.currentSelectPage = 1;
              that.formLabelAlign.type_code = that.component_type_list[0].value;
              that.getSecondSelectList();
            }
          } else {
          }
        },
        function(error) {}
      );
    },
  },
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


